body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  font-family: Roboto Condensed, sans-serif;
}

.menu-screen {
  background-image: linear-gradient(#424f9d, #7eaadc);
  grid-template-rows: repeat(8, 55px);
  grid-template-columns: repeat(5, 60px);
  gap: 6px;
  margin-right: 10px;
  padding: 4px;
  display: grid;
}

.menu-screen-path {
  text-align: center;
  white-space: nowrap;
  justify-content: center;
  padding-top: 20px;
  display: flex;
}

.search-bar {
  background-color: #fff;
  grid-template-columns: 200px auto 200px;
  align-items: center;
  margin: 0 30px;
  display: grid;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

.search-box {
  white-space: nowrap;
  width: 100%;
  padding: 0 80px;
}

.search-box input {
  width: 80%;
  text-transform: uppercase;
  border: 2px solid #132262;
  font-family: Roboto Condensed;
  font-size: 2em;
}

.clear {
  color: #132262;
  cursor: pointer;
  margin-left: 10px;
  font-size: xx-large;
}

.search-result {
  cursor: pointer;
  margin-top: 20px;
}

.path-segment {
  padding: 10px 15px;
  display: inline-block;
}

.keyword {
  border: 1px solid #000;
  border-radius: 10px;
  margin-left: 10px;
  padding: 5px;
  font-size: small;
}

.search-results {
  width: 400px;
  white-space: nowrap;
  margin: 0 auto;
}

.single-search-result {
  text-align: center;
  white-space: nowrap;
  margin: 0 auto;
}

@-webkit-keyframes blinker {
  50% {
    background-color: #fff;
  }
}

@keyframes blinker {
  50% {
    background-color: #fff;
  }
}

.menu-button {
  color: #000;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #fff;
  justify-content: center;
  align-items: center;
  font-family: helvetica;
  font-size: 9px;
  font-weight: bold;
  display: flex;
  position: relative;
}

.menu-button.selected-true {
  -webkit-animation: 1s linear infinite blinker !important;
  animation: 1s linear infinite blinker !important;
}

.menu-button.span-1 {
  grid-area: span 1 / span 1;
}

.menu-button.span-2 {
  grid-area: span 1 / span 2;
}

